@import "../../styles/colors";
@import "../../styles/variables";

.card {
  flex: 1 0 100%;
  position: relative;
  background-size: cover;
  background-position: center;
  border-radius: 10px;

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to left, rgba(0, 0, 0, 0), rgba(15, 20, 32, 0.79));
    border-radius: 10px
  }

  &__description {
    padding: 32px 27px;
    position: relative;
    z-index: 1;
  }

  &__title {
    margin-top: 65px;
    font-size: 40px;
    font-weight: 800;
    line-height: 40px;
  }

  &__promo {
    font-size: 16px;
    font-weight: 800;
    line-height: 19.2px;
    text-align: left;
    margin-top: 22px;
  }

  &__btn {
    margin-top: 22px;
  }
}

@media all and (min-width: $tablet) {
  .card {
    flex: 1 0 46.5%;
  }
}

@media all and (min-width: $desktop) {
  .card {
    &__description {
      max-width: 589px;
      padding: 35px;
    }

    &__title {
      margin-top: 10px;
      font-size: 46px;
      line-height: 46px;
    }

    &__promo {
      font-size: 24px;
      line-height: 28.8px;
    }

    &:first-child &__title,
    &:last-child &__title {
      margin-top: 0;
      font-size: 74px;
      line-height: 74px;
    }

    &:first-child &__promo,
    &:last-child &__promo {
      font-size: 26px;
      line-height: 31.2px;
    }

    &:first-child,
    &:last-child {
      flex: 1 0 100%;
    }
  }
}