@import "../../styles/colors";
@import "../../styles/variables";

.tile {
  &__container {
    background: linear-gradient(#0D1E48, #304571);
    padding: 50px 20px;
  }

  &__title {
    text-transform: capitalize;
    font-size: 18px;
    font-weight: 800;
    line-height: 21.6px;
    color: $white;
  }

  &__list {
    margin-top: 34px;
    color: $white;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
  }
}

@media all and (min-width: $tablet) {
  .tile {
    &__container {
      padding: 55px 30px 53px;
    }

    &__title {
      font-size: 24px;
      line-height: 28.8px;
    }

    &__list {
      margin-top: 30px;
      color: $white;
      display: flex;
      flex-wrap: wrap;
    }
  }

}

@media all and (min-width: $desktop) {
  .tile {
    &__container {
      padding: 74px 80px 58px
    }

    &__title {
      font-size: 32px;
      line-height: 38.4px;
    }

    &__list {
      margin-top: 40px;
      gap: 40px;
    }
  }
}