@import "../../styles/colors";
@import "../../styles/variables";

.nav {
  &__container {
    padding: 6px 20px;
    background-color: $black;
    display: flex;
    align-items: center;
  }

  &__link {
    margin-right: auto;
  }

  &-list {
    display: none;
  }
}

.burger-menu .toggle-menu {
  display: inline-block;
  cursor: pointer;
  opacity: 0;
}

.menu-list {
  position: absolute;
  display: none;
}

.burger-menu input:checked ~ .menu-list,
.toggle-menu:checked ~ .menu-list {
  display: block;
}

[for="switch-menu"] .menu__close {
  display: none;
  visibility: hidden;
}

.toggle-menu:checked ~ [for="switch-menu"] .menu__open {
  display: none;
  visibility: hidden;
}

.toggle-menu:checked ~ [for="switch-menu"] .menu__close {
  display: inline-block;
  visibility: visible;
}

.menu-list {
  padding: 10px 15px;
  background: $black;
  right: 0;
  top: 48px;

  &__item {
    padding: 5px;
  }

  &__link {
    font-size: 16px;
    color: $white;
    text-decoration: none;
  }
}

@media all and (min-width: $tablet) {
  .nav {
    &__container {
      padding: 27px 34px;
    }

    .burger-menu {
      display: none;
    }

    .nav-list {
      display: flex;
      padding: 0;

      &__item {
        list-style-type: none;
        margin-left: 20px;
      }

      &__link {
        color: $white;
        text-decoration: none;
        padding: 0 12px;
        font-size: 18px;
        font-weight: 700;
        line-height: 21.6px;

        &:hover {
          background: linear-gradient(98.25deg, $yellow 18.56%, $pink 76.02%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
      }
    }
  }

  .nav-list__buy {
    background-color: $white;
    width: 24px;
    height: 24px;
    mask: url("../../assets/icons/buy-hover.svg") 0 0/24px 24px;
    mask-repeat: no-repeat;
    display: inline-block;

    &:hover {
      background: linear-gradient(98.25deg, $yellow 18.56%, $pink 76.02%);
    }
  }
}

@media all and (min-width: $desktop) {
  .nav {
    &__container {
      padding: 35px 81px 20px;
    }

    & &-list__item {
      margin-left: 31px;
    }

    &__logo {
      width: 104px;
    }
  }
}
