@import "../../styles/colors";
@import "../../styles/variables";

.banner {
  width: 100%;
  height: 451px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%),
  url("../../assets/images/earth.png") no-repeat top -141px right -8px,
  url("../../assets/images/background-space-1.png") no-repeat center;

  &__container {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    padding: 0 34px;
  }

  &__title {
    font-size: 36px;
    font-weight: 800;
    line-height: 30px;
    color: $white;

    span {
      color: $pink;
    }
  }

  &__description {
    margin-top: 17px;
    font-size: 16px;
    font-weight: 800;
    line-height: 19.2px;
    color: $white;

    span {
      color: $yellow;
    }
  }

  .btn__banner {
    margin: 22px 0px 44px;
  }
}

@media all and (min-width: $tablet) {
  .banner {
    width: 100%;
    min-height: 420px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%),
    url("../../assets/images/earth.png") no-repeat right -133px top 2px,
    url("../../assets/images/background-space-1.png") no-repeat center;

    &__container {
      justify-content: center;
      align-items: flex-start;
      min-width: 410px;
      padding-left: 30px;
    }

    &__title {
      margin-top: 41px;
      font-size: 48px;
      line-height: 48px;

      &_text_highlight {
        color: $pink;
      }
    }

    &__description {
      margin-top: 22px;
      font-size: 22px;
      line-height: 26.4px;

      &_text_highlight {
        color: $yellow;
      }
    }
  }
}


@media all and (min-width: $desktop) {
  .banner {
    min-height: 483px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%),
    url("../../assets/images/earth.png") no-repeat right 63px top 54px,
    url("../../assets/images/background-space-1.png") no-repeat center;

    &__container {
      padding-left: 81px;
      max-width: 693px;
    }

    &__title {
      margin-top: 21px;
      font-size: 72px;
      line-height: 72px;

      &_text_highlight {
        color: $pink;
      }
    }

    &__description {
      font-size: 32px;
      line-height: 38.4px;

      &_text_highlight {
        color: $yellow;
      }
    }
  }
}