@import "../../styles/colors";
@import "../../styles/variables";

.promo {
  &__title {
    margin-top: 75px;
    font-size: 18px;
    line-height: 21.6px;
    font-weight: 800;
    color: $white;
  }

  &__description {
    margin-top: 22px;
    font-size: 14px;
    font-weight: 700;
    line-height: 16.8px;
    color: $white;
    opacity: .6;
  }

  &__toggle {
    display: inline-block;
    margin-top: 17px;
    font-size: 14px;
    font-weight: 800;
    line-height: 16.8px;
    color: $yellow;
    text-decoration: none;
    cursor: pointer;
  }

  [for="open-menu"] span:last-of-type {
    display: none;
    visibility: hidden;
  }

  .promo_switch {
    display: none;

    &:checked ~ .promo__description-more {
      display: block;
    }

    &:checked ~ [for="open-menu"] span:first-of-type {
      display: none;
      visibility: hidden;
    }

    &:checked ~ [for="open-menu"] span:last-of-type {
      display: block;
      visibility: visible;
    }
  }

  &__description-more {
    display: none;
    margin-top: 22px;
    font-size: 14px;
    font-weight: 700;
    line-height: 16.8px;
    color: $white;
    opacity: .6;
  }
}

@media all and (min-width: $tablet) {
  .promo {
    &__title {
      margin-top: 55px;
      font-size: 24px;
      line-height: 28.8px;
    }

    &__description,
    &__description-more {
      margin-top: 13px;
      font-size: 16px;
      line-height: 19.2px;
    }

    &__link {
      margin-top: 20px;
      font-size: 16px;
      line-height: 19.2px;
    }
  }
}

@media all and (min-width: $desktop) {
  .promo {
    &__title {
      margin-top: 106px;
      font-size: 32px;
      line-height: 38.4px;
    }

    &__description,
    &__description-more {
      margin-top: 17px;
      font-size: 20px;
      line-height: 24px;
    }

    &__toggle {
      margin-top: 17px;
      font-size: 20px;
      line-height: 20px;
    }
  }
}