@import "../../styles/colors";
@import "../../styles/variables";

.btn {
  font-size: 16px;
  font-weight: 700;
  line-height: 19.2px;
  display: inline-block;
  text-align: center;
  max-width: 161px;
  padding: 7px 40px;
  background-color: transparent;
  border-radius: 100px;
  text-decoration: none;

  &_default {
    line-height: 19px;
    background-color: $yellow;
    color: $black;

    &:hover {
      background-color: $yellow-dark;
    }
  }

  &_transparent {
    color: $yellow;
    border: 2px solid $yellow;
    background-color: transparent;


    &:hover {
      color: $yellow-dark;
      background-color: transparent;
    }
  }
}

@media all and (min-width: $tablet) {
  .btn {
    &_default {
      font-size: 22px;
      line-height: 26.4px;
      padding: 14px 40px;
    }

    &_transparent {
      font-size: 16px;
      line-height: 19.2px;
      padding: 7px 40px;

    }
  }
}

@media all and (min-width: $desktop) {
  .btn_transparent {
    font-size: 22px;
    line-height: 26.4px;
    padding: 14px 40px;
  }
}